<template>
  <div
    class="sucsac-diemso-wrapper"
    v-bind:class="{ active: isPlaying === nguoiChoiId }"
  >
    <h4>{{ title }}</h4>
    <div class="total">
      <p>Tổng điểm</p>
      <span>{{ tongDiem }}</span>
    </div>
    <input type="text" v-bind:value="tongDiemMotLuot" readonly />
  </div>
</template>

<script>
export default {
  name: "comp-sucsac-diemso",
  props: {
    nguoiChoiId: {
      type: Number,
      default: undefined,
    },
    isPlaying: {
      type: Number,
      default: 0,
    },
    title: {
      type: String,
      default: "Điểm số",
    },
    tongDiem: {
      type: Number,
      default: 0,
    },
    tongDiemMotLuot: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {};
  },
};
</script>
