<template>
  <div>
    <div class="box-title">
      <h3>Axios</h3>
      <p>Kết nối API với Axios</p>
    </div>
    <ul>
      <li v-for="(item, index) in todos" v-bind:key="index">{{ item.name }}</li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "comp-axios",
  data() {
    return {
      todos: [],
    };
  },
  async created() {
    try {
      await axios({
        method: "get",
        url: process.env.SERVER + `todos`,
      }).then(function(response) {
        this.$set(this.todos, 0, response.data)
      });
    } catch (e) {
      console.log(e);
    }
  },
};
</script>
