<template>
  <header class="header">
    <h2>
      {{ title }} - <span>{{ userName }}</span>
    </h2>
  </header>
</template>

<script>
export default {
  name: "comp-header",
  props: {
    userName: {
      type: String,
      default: "Anonymous",
    },
  },
  data() {
    return {
      title: "Header",
    };
  },
};
</script>

<style lang="scss">
  header {
    h2 {
      margin: 0px;
      padding: 0 0 10px;
      text-transform: uppercase;
    }
  }
</style>
