<template>
  <div class="box-wrapper gamesucsac-wrapper">
    <div class="title-box">
      <h4>Game xúc xắc 2 người chơi</h4>
    </div>
    <div class="box-gamesucsac-view">
      <CompSucSacDiemSo
        v-bind:nguoiChoiId="0"
        v-bind:isPlaying="isPlaying"
        v-bind:title="thongTinCaNhan[0]"
        v-bind:tongDiem="tongDiemCaNhan[0]"
        v-bind:tongDiemMotLuot="tongDiemMotLuotChoi[0]"
      />
      <CompSucSac v-bind:sucSacFinal="sucSacFinal" />
      <CompSucSacDiemSo
        v-bind:nguoiChoiId="1"
        v-bind:isPlaying="isPlaying"
        v-bind:title="thongTinCaNhan[1]"
        v-bind:tongDiem="tongDiemCaNhan[1]"
        v-bind:tongDiemMotLuot="tongDiemMotLuotChoi[1]"
      />
    </div>
    <CompSucSacControl
      v-on:handleNewPlaying="handleNewPlaying"
      v-on:handleGieoSucSac="handleGieoSucSac"
      v-on:handleLuuDiem="handleLuuDiem"
      v-bind:nguoiChoi="thongTinCaNhan[isPlaying]"
    />
    <div v-bind:class="[`dialog-sucsac-wrapper`, { show: isDialogOpen }]">
      <div class="dialog-sucsac-box">
        <div>
          <p>Hướng dẫn chơi</p>
          <fieldset>
            <legend>Thông tin người chơi:</legend>
            <div class="form-control">
              <label for="fname">Tên người chơi 1:</label>
              <input type="text" v-model.trim="thongTinCaNhan[0]" />
            </div>
            <div class="form-control mb15">
              <label for="fname">Tên người chơi 2:</label>
              <input type="text" v-model.trim="thongTinCaNhan[1]" />
            </div>
            <div class="form-control mb0">
              <button v-on:click="funcXacNhanThongTin">
                <span>Xác nhận</span>
              </button>
              <button v-on:click="funcXacNhanChoiTiep" class="btn-default">
                <span>Tiếp tục chơi</span>
              </button>
            </div>
          </fieldset>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CompSucSac from "./SucSac.vue";
import CompSucSacControl from "./SucSacControl.vue";
import CompSucSacDiemSo from "./SucSacDiemSo.vue";
export default {
  name: "comp-gamesucsac",
  data() {
    return {
      isDialogOpen: false,
      isPlaying: 0,
      thongTinCaNhan: ["User 1", "User 2"],
      tongDiemMotLuotChoi: [0, 0],
      tongDiemCaNhan: [0, 0],
      sucSacFinal: [1, 1],
    };
  },
  methods: {
    funcXacNhanChoiTiep() {
      if (!this.thongTinCaNhan[0] || !this.thongTinCaNhan[1]) {
        return false;
      }

      this.isDialogOpen = !this.isDialogOpen;
    },
    funcXacNhanThongTin() {
      if (!this.thongTinCaNhan[0] || !this.thongTinCaNhan[1]) {
        return false;
      }

      let tongDiemCaNhanReset = [0, 0];
      let tongDiemMotLuotChoiReset = [0, 0];
      let newPlaying = [this.thongTinCaNhan[0], this.thongTinCaNhan[1]];

      this.isDialogOpen = !this.isDialogOpen;
      this.isPlaying = 0;
      this.sucSacFinal = [1, 1];
      this.tongDiemCaNhan = [...tongDiemCaNhanReset];
      this.tongDiemMotLuotChoi = [...tongDiemMotLuotChoiReset];
      this.thongTinCaNhan = [...newPlaying];
    },
    handleNewPlaying() {
      this.isDialogOpen = !this.isDialogOpen;
    },
    handleGieoSucSac() {
      let sucSac1 = Math.floor(Math.random() * 6) + 1;
      let sucSac2 = Math.floor(Math.random() * 6) + 1;
      let sucSacRandom = [sucSac1, sucSac2];
      this.sucSacFinal = [...sucSacRandom];
      if (sucSacRandom.filter((item) => item === 1).length !== 0) {
        this.handleQuayTrungMotNut();
      } else {
        this.tongDiemMotLuotChoi[this.isPlaying] = this.$set(
          this.tongDiemMotLuotChoi,
          this.isPlaying,
          this.tongDiemMotLuotChoi[this.isPlaying] +
            this.sucSacFinal.reduce((a, b) => a + b, 0)
        );
      }
    },
    handleLuuDiem() {
      return new Promise((resolve) => {
        this.tongDiemCaNhan[this.isPlaying] = this.$set(
          this.tongDiemCaNhan,
          this.isPlaying,
          this.tongDiemCaNhan[this.isPlaying] +
            this.tongDiemMotLuotChoi[this.isPlaying]
        );
        resolve(true);
      }).then(this.handleChuyenNguoiChoi());
    },
    handleChuyenNguoiChoi() {
      this.isPlaying = this.isPlaying === 0 ? 1 : 0;
      this.sucSacFinal = [1, 1];
      this.tongDiemMotLuotChoi = [0, 0];
    },
    handleQuayTrungMotNut() {
      return new Promise((resolve) => {
        let tongDiemMotLuotChoiReset = [0, 0];
        this.tongDiemCaNhan[this.isPlaying] = this.$set(
          this.tongDiemCaNhan,
          this.isPlaying,
          0
        );
        this.sucSacFinal = [1, 1];
        this.tongDiemMotLuotChoi = [...tongDiemMotLuotChoiReset];
        resolve(true);
      }).then(() => {
        this.isPlaying = this.isPlaying === 0 ? 1 : 0;
      });
    },
  },
  computed: {
    user1: {
      get() {
        return this.user1;
      },
      set(value) {
        console.log(value);
      },
    },
  },
  components: {
    CompSucSac,
    CompSucSacControl,
    CompSucSacDiemSo,
  },
};
</script>
