<template>
  <div class="box-wrapper">
    <div class="title-box">
      <h4>Slot và Life cycle</h4>
      <p>Truyền HTML vào component và Vòng đời của Vue instance</p>
    </div>
    <CompSlot>
      <div class="tabs-wrapper">
        <ul class="tabs-title">
          <li
            v-for="(item, index) in tabsList"
            v-bind:key="index"
            v-bind:class="{ active: tabActive === index }"
            v-on:click="setActiveTab(index)"
          >
            {{ item.name }}
          </li>
        </ul>
        <div class="tabs-content">
          <div
            class="tab-content"
            v-for="(item, index) in tabsLists"
            v-bind:key="index"
          >
            {{ item.content }}
          </div>
        </div>
      </div>
    </CompSlot>
  </div>
</template>
<script>
import CompSlot from "./CompSlot.vue";
export default {
  name: "comp-slot-and-life-cycle",
  data() {
    return {
      tabActive: 0,
      tabsList: [
        {
          name: "tab #1",
          content: "Nội dung tab #1",
        },
        {
          name: "tab #2",
          content: "Nội dung tab #2",
        },
        {
          name: "tab #3",
          content: "Nội dung tab #3",
        },
      ],
    };
  },
  methods: {
    setActiveTab(index) {
      this.tabActive = index;
    },
  },
  computed: {
    tabsLists() {
      return this.tabsList.filter((item, index) => index === this.tabActive);
    },
  },
  components: {
    CompSlot,
  },
};
</script>
