<template>
  <tr>
    <td>{{ item.id }}</td>
    <td v-on:click="changeUserName">{{ item.name }}</td>
    <td>{{ item.fromDate }}</td>
    <td><button v-on:click="deleteUserItem">Delete</button></td>
  </tr>
</template>

<script>
export default {
  name: "comp-item",
  props: {
    item: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {};
  },
  methods: {
    changeUserName(e) {
      this.$emit("handleChangeUserName", this.item.name);
    },
    deleteUserItem(e) {
      this.$emit("handleDeleteUserItem", this.item.id);
    },
  },
};
</script>
