<template>
  <div class="box-wrapper">
    <div class="title-box">
      <h4>Props và Event up</h4>
      <p>Truyền data từ cha xuống con và ngược lại</p>
    </div>
    <table class="mb10">
      <tr>
        <th>Id</th>
        <th>Name</th>
        <th>FromDate</th>
        <th></th>
      </tr>
      <CompItem
        v-for="(item, index) in congViecDS"
        v-bind:key="index"
        v-bind:item="item"
        v-on:handleChangeUserName="handleChangeUserName"
        v-on:handleDeleteUserItem="handleDeleteUserItem"
      />
    </table>

    <div class="title-box">
      <h4>Fef</h4>
      <p>Tham chiếu đến phần tử html</p>
    </div>
    <input type="file" ref="chonHinh" style="display: none" />
    <button type="button" v-on:click="chooseImages">Chọn hình</button>
  </div>
</template>

<script>
import CompItem from "./CompItem.vue";
import moment from "moment";
export default {
  name: "comp-list",
  data() {
    return {
      congViecDS: [
        {
          id: 1,
          name: "Demo #1",
          fromDate: moment(new Date()).format("DD/MM/Y"),
        },
        {
          id: 2,
          name: "Demo #2",
          fromDate: moment(new Date())
            .add(1, "day")
            .format("DD/MM/Y"),
        },
        {
          id: 3,
          name: "Demo #3",
          fromDate: moment(new Date())
            .add(2, "day")
            .format("DD/MM/Y"),
        },
      ],
    };
  },
  components: {
    CompItem,
  },
  methods: {
    handleChangeUserName(data) {
      this.$emit("handleChangeUserName", data);
    },
    handleDeleteUserItem(data) {
      let indexItem = -1;
      this.congViecDS.forEach((item, index) => {
        if (item.id === data) {
          indexItem = index;
        }
      });
      if (indexItem !== -1) {
        this.congViecDS.splice(indexItem, 1);
      }
    },
    chooseImages() {
      this.$refs.chonHinh.click();
    },
  },
};
</script>
