<template>
  <div class="sucsac-control-wrapper">
    <p class="user-info">
      người chơi:<span>{{ nguoiChoi }}</span>
    </p>
    <div class="button-items">
      <div class="button-item">
        <button class="while" v-on:click="clickNewPlaying">
          <span class="icon icon-reload"></span>
          <span>Chơi mới</span>
        </button>
      </div>
      <div class="button-item">
        <button v-on:click="clickGieoSucSac">
          <span class="icon icon-theme-1xucxac"></span>
          <span>Gieo súc sắc</span>
        </button>
      </div>
      <div class="button-item">
        <button class="green" v-on:click="clickLuuDiem">
          <span class="icon icon-diskette"></span>
          <span>Lưu điểm</span>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "comp-sucsac-control",
  props: {
    nguoiChoi: {
      type: String,
      default: null,
    },
  },
  data() {
    return {};
  },
  methods: {
    clickNewPlaying() {
      this.$emit("handleNewPlaying");
    },
    clickGieoSucSac() {
      this.$emit("handleGieoSucSac");
    },
    clickLuuDiem() {
      this.$emit("handleLuuDiem");
    },
  },
};
</script>
