import Vue from "vue";
import Router from "vue-router";
import CompGameSucSac from "./../components/gamesucsac/index.vue";
import CompSlotAndLifeCycle from "./../components/slotvalifecycle/CompSlotAndLifeCycle.vue";
import compHome from "./../components/home/index.vue";
import CompList from "./../components/propsvaeventup/CompList.vue";
import CompAxios from "./../components/axios/index.vue";

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: "/", // / path của route
      name: "Trang chủ", // tên route
      component: compHome, // component route sử dụng
    },
    {
      path: "/danh-sach",
      name: "Render danh sách",
      component: CompList,
    },
    {
      path: "/slot-lifecycle",
      name: "Slot và lifecycle",
      component: CompSlotAndLifeCycle,
    },
    {
      path: "/game-xuc-xac",
      name: "Game xúc xắc",
      component: CompGameSucSac,
    },
    {
      path: "/axios",
      name: "Axios",
      component: CompAxios,
    },
  ],
  components: {
    compHome,
    CompList,
    CompGameSucSac,
    CompSlotAndLifeCycle,
    CompAxios
  },
});
